.nuclear{position:relative;display:block;height:70px;width:70px;border-radius:50%;background:#2980b9;-webkit-animation:ripple 2s linear infinite;animation:ripple 2s linear infinite}.nuclear::after,.nuclear::before{content:"";display:block;height:70px;width:70px;border-radius:50%}.nuclear::before{background:#000;-webkit-animation:pulse 2s ease-out infinite;animation:pulse 2s ease-out infinite}@-webkit-keyframes ripple{0%{box-shadow:0 0 0 0 rgba(204,204,204,0.4)}50%{box-shadow:0 0 2px 3em rgba(204,204,204,0.2)}100%{box-shadow:0 0 7em 7em rgba(204,204,204,0.1)}}@keyframes ripple{0%{box-shadow:0 0 0 0 rgba(204,204,204,0.4)}50%{box-shadow:0 0 2px 3em rgba(204,204,204,0.2)}100%{box-shadow:0 0 7em 7em rgba(204,204,204,0.1)}}@-webkit-keyframes pulse{0%{-webkit-transform:scale(0.1);transform:scale(0.1)}100%{-webkit-transform:scale(1);transform:scale(1)}}@keyframes pulse{0%{-webkit-transform:scale(0.1);transform:scale(0.1)}100%{-webkit-transform:scale(1);transform:scale(1)}}
